
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import cookie from 'vux/src/tools/cookie/index.js'


import axios from 'axios'
export default {
data(){
return {
userId:cookie.get('userId'),
contents:{title:'党建'},
info:{name:'王大陆',cardId:'6282451745218551527'},
collect:[],
pay:''
}
},
components:{
XHeader,
ViewBox,
Group,
Cell,
Flexbox,
FlexboxItem
},
mounted(){
axios.get('ppartymemberdues/queryByUserId',{
params:{
userid:this.userId
}
}).then(res =>{
this.collect = res.data;
this.collect.sort((x,y) => {
return (x.month > y.month ? 1 : -1)

})
}).catch(err =>{
console.log('fail'+err);
})
},
methods:{
backhome(){
this.$router.push({
path:'/'
})
}
}

}



